<template>
	<section id="userCenter">
    <span class="title">{{$t('userCenter.user')}}</span>
	  <el-form :model="ruleForm" :rules="rules" ref="ruleForm"  >
		  <el-form-item :label="$t('userCenter.oldPwd')" prop="originalPassword" >
		    <el-input type="password" v-model="ruleForm.originalPassword" ></el-input>
		  </el-form-item>
		  <el-form-item :label="$t('userCenter.newPwd')" prop="newPassword">
		    <el-input type="password"  v-model="ruleForm.newPassword"></el-input>
		  </el-form-item>
		  <el-form-item :label="$t('userCenter.coptNewPwd')" prop="newConfirmedPassword">
		    <el-input type="password" v-model="ruleForm.newConfirmedPassword"></el-input>
		  </el-form-item>
      <el-col :span="24" style="padding-bottom: 0px" class="toolbar">
        <el-button class="warning" v-on:click="modifyPassword">{{$t('userCenter.sure')}}</el-button>
      </el-col>
	  </el-form>
  </section>
</template>
   
<script>
import {ajax,dateStr2GmtTimeStamp,getDataYear} from '../../common/js/common.js'
export default {

   data(){
     var checkOldPass = (rule,value,callback) =>{
       if(!value){
         return callback(new Error("请输入原始密码"))
       }else if(!(/^[0-9a-zA-Z]{5,20}$/g.test(value))){
         return callback(new Error("原密码格式有误"))
       }else{
         callback()
       }
     };
     var checkNewPass = (rule,value,callback) =>{
       if(value===""){
         callback(new Error("请输入密码"))
       }else if(!(/^[0-9a-zA-Z]{5,20}$/g.test(value))){
          return callback(new Error("密码格式有误"))
       }else{
         if(this.ruleForm.newConfirmedPassword !== ""){
           this.$refs.ruleForm.validateField('newConfirmedPassword')
         }
        callback()
       }
     };
     var checkNewPass2 = (rule,value,callback) =>{
       if(value == ""){
         callback(new Error('请再次输入密码'))
       }else if(value !== this.ruleForm.newPassword){
         callback(new Error('两次输入的密码不一致!'))
       }else{
         callback()
       }

     }
     return{
     	ruleForm: {
          originalPassword: '',
          newPassword:"",
          newConfirmedPassword:"",
        },
        rules: {
         originalPassword:[
           {validator:checkOldPass,trigger:'blur'}
           ],
         newPassword:[
           {validator:checkNewPass,trigger:'blur'}
         ],
         newConfirmedPassword:[
           {validator:checkNewPass2,trigger:'blur'}
         ]
          
        }
      };
   },
   methods:{
    // 用户修改个人密码
    modifyPassword:function(){
      // if(this.newPassword !== this.newConfirmedPassword){
      //   this.$notify({
      //     title:"提示",
      //     message:"两次密码不一致",
      //     type:'warning'
      //   })
      // }else{
        this.$refs.ruleForm.validate((valid) => {
          if (valid) {
                ajax(this,this.ApiConfig.userCenter.modifyPassword,this.ruleForm,(res) =>{
                  if(res.code == 200){
                    this.$notify({
                      title:"提示",
                      message:res.message+"需要重新登陆",
                      type:"success"
                    })
                    this.$router.push({ path:'/login'})
                  }else{
                    this.$notify({
                      title:"提示",
                      message:res.message,
                      typ:"warning"
                    })
                    return  false;
                  }
                },"POST")
                  } else {
            // console.log('error submit!!');
            return false;
          }
        });
        // }
    },
	}
}
</script>
<style scoped lang="postcss">


  .toolbar  .warning {
    width: 120px;
    height: 30px;
    border-radius: 4px;
    font-size: 14px;
    background: #bf9764;
    color: #ffffff;

  }
  
</style>
<style>
 #userCenter .el-input {
  width: 320px;
 }
 #userCenter .el-form-item__label {
    font-size: 14px;
    width: 140px!important;
    position: relative;
    margin-bottom: 15px;
  }
  #userCenter .el-form-item__label::after{
    content: "*";
    color: red;
    /* position: absolute;
    left: 0px;
    top: 2px; */
  }
#userCenter  .el-form-item__error{
    left:150px;
  }
</style>







